import React, { useMemo } from "react"
import { useParams } from "react-router"
import { CustomDocumentHeaderProps } from "@rp/react/web/document-viewer"
import { DocumentViewerPage } from "@rp/react/web/document-viewer"
import { Box } from "@rp/react/web/mui/core"
import { ChatPaneButton } from "../ChatPaneButton"
import { ClosePaneButton } from "../ClosePaneButton"
import { ExpandPaneButton } from "../ExpandPaneButton"
import { FilesDashPane } from "../FilesDashPane"

type DocumentPaneProps = {
  width: string | null
  close: () => void
  expandPane: () => void
  compressPane: () => void
  openChatPane: () => void
  isChatPaneOpen: boolean
}

function DocumentPane({
  width,
  close,
  expandPane,
  compressPane,
  openChatPane,
  isChatPaneOpen,
}: DocumentPaneProps) {
  const { id: documentId } = useParams()
  const customDocumentHeaderProps: CustomDocumentHeaderProps = useMemo(
    () => ({
      variant: "pane",
      customButtons: [
        <ChatPaneButton chat={openChatPane} key="chat button" disabled={isChatPaneOpen} />,
        <ExpandPaneButton expand={expandPane} key="expand button" />,
        <ClosePaneButton close={close} key="close button" />,
      ],
    }),
    [close, expandPane, isChatPaneOpen, openChatPane],
  )

  if (!width) return null

  if (width === "100%") {
    return (
      <DocumentViewerPage
        documentId={documentId}
        backTitle=""
        disableBackButton
        pdfHeaderOffset={61}
        customBackClick={compressPane}
      />
    )
  }

  return (
    <FilesDashPane width={width} minWidth="350px">
      <Box
        sx={theme => ({
          width: "100%",
          height: "calc(100vh - 82px)",
          ...theme.scrollbarStyles,
          overscrollBehavior: "contain",
          overflowY: "scroll",
          overflowX: "hidden",
          scrollbarGutter: "stable",
        })}
      >
        <DocumentViewerPage
          disableDocumentDiscovery
          backTitle=""
          disableBackButton
          customDocumentHeaderProps={customDocumentHeaderProps}
          documentId={documentId}
        />
      </Box>
    </FilesDashPane>
  )
}

export { DocumentPane }
